.Landing {
  position: relative;
  margin: 0 120px;
  margin-top: calc(15vh + 135px);

  @media only screen and (max-width: 900px) {
    margin: 0;
    margin-top: calc(15vh + 135px);
  }

  &::after {
    content: "";
    display: block;
    position: absolute;

    left: -100vw;
    bottom: -6vw;
    width: 200vw;
    height: 50%;

    background-image: url("../assets/bg-texture.png");
    background-position: 0% 100%;
    background-size: 15px auto;
    background-repeat: repeat-x;
    z-index: -200;
  }

  h1 {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    margin: 0 5vw;
  }

  h3, h4 {
    max-width: 50vw;
    margin: 0 auto;
  }

  .Art {
    position: absolute;
    z-index: -100;
    width: 100%;
    height: 50vh;
    left: 0;
    display: flex;

    svg {
      position: absolute;
      height: 55vh;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -40%);

      path {
        transition: fill 1s, filter 1s;
      }
    }
  }

  .CardList {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 0 2vw;

    @media only screen and (max-width: 900px) {
      grid-template-columns: 1fr;
    }

    .Card {
      display: flex;
      flex-flow: column;
      border-radius: 3px;
      border: solid 2px #8b0430;
      align-items: center;
      padding: 6vmin 8vmin;
      margin-right: -1.5px;
      margin-bottom: -1.5px;

      *:not(:last-child) {
        margin-bottom: 4vmin;
        margin-top: 0;
      }

      .Dynamic {
        display: flex;
        flex: 1 1 0;
        flex-flow: row nowrap;
        justify-content: center;
        align-items: center;
      }

      img {
        height: max(calc(1.85 * 1vw + 63px), 70px);
      }

      @media only screen and (max-width: 900px) {
        margin-right: 0px;
        margin-bottom: -1.5px;
      }
    }
  }

  .Expander {
    margin: 0.5vw 4vw;

    @media only screen and (max-width: 900px) {
      margin: 0.5vw 0vw;
    }
  }

  .Footer {
    position: absolute;
    left: -120px;
    right: 0;
    height: 25vh;
    z-index: 20000;

    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    overflow: hidden;

    background: #0c0a27;
    margin-top: 5vw;

    @media only screen and (max-width: 900px) {
      flex-flow: column nowrap;
      height: auto;
      left: 0;
      right: 0;
    }

    .Logo {
      position: relative;
      height: 100%;
      width: 30%;
      background: #8b0430;

      img {
        position: absolute;
        object-fit: contain;
        max-width: 35%;
        max-height: 56%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        @media only screen and (max-width: 900px) {
          left: 50%;
          max-width: 100%;
          max-height: 50%;
        }
      }

      @media only screen and (max-width: 900px) {
        width: 100%;
        height: 25vw;
      }
    }

    .LinksList {
      display: flex;
      flex-flow: row nowrap;
      align-items: stretch;
      padding: 6vw;

      @media only screen and (max-width: 900px) {
        flex-flow: column nowrap;
        align-items: flex-start;
      }

      figure {
        display: flex;
        flex-flow: column nowrap;
        justify-content: flex-start;

        flex: 1 1 auto;
        max-width: 35ch;
        margin: 0;

        figcaption {
          flex: 0 0 auto;

          h4 {
            margin: 0;
            max-width: auto;
            text-align: left;
          }
        }

        .Content {
          flex: 1 1 0;
        }

        &:not(:last-child) {
          margin-right: 6vw;
        }

        ul {
          list-style: none;
          padding: 0;
        }

        li a, p {
          font-size: 12px;
        }

        @media only screen and (max-width: 900px) {
          margin: 0;
          margin-bottom: 20px;
        }
      }
    }
  }
}
